
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: hidden;
  position: fixed;
}
#container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
#contolContainer {
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.fileContainer {
  padding-bottom: 8px;
}
#file1,
#file2 {
  position: relative;
}

.fileContainer,
.folderContainer {
  display: flex;
}

#cnsWebViewerContainer,
#cnsWebViewer {
  display: flex;
  flex-grow: 1;
}
